.checkbox_title{
    font-family: Lab Grotesque,sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: #000000;
}
.checkbox_flex{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    max-height: 57px;
}
.work_flex{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}
.checkbox_item{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    min-width: 488px;
    border-bottom: 1px solid rgba(223, 227, 230, 0.54);

    min-height: 56px;
}


.checkbox_flex:last-child{
    margin-bottom: 20px;
}

