.checkbox_item__0{
    border: 1px solid #808080;
    width: 20px;
    height: 20px;
    border-radius: 5px;
    margin-right: 12px;
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.checkbox_item__0:hover{
    border: 1px solid black ;
}
.checkbox_item__0 img {
    min-width: 14px;
    min-height: 11px;
    background: url("checkbox.svg") no-repeat ;
}
.checkbox_item_0_checked img{
    min-width: 14px;
    min-height: 11px;
    background: url("checkbox.svg") no-repeat ;

}


.checkbox_item_0_checked{
    border: 1px solid #808080;
    width: 20px;
    height: 20px;
    border-radius: 5px;
    margin-right: 12px;
    background: linear-gradient(255.35deg, #DC3131 0.83%, rgba(255, 79, 79, 0) 108.93%), #FF5E56;
    display: flex;
    align-items: center;
    justify-content: center;
}
.checkbox_label_item_1{
    margin-right: 5px;
}
.checkbox_label_item_2{
    opacity: 50%;
}



